
import { defineComponent } from 'vue';
import { mapMutations, mapState } from 'vuex';
import { Category } from '@/types/interfaces';
import Dropdown from '@/components/Dropdown.vue';
import Search from '@/components/Search.vue';
import CATEGORIES from '@/assets/categories';

interface Data {
  CATEGORIES: Array<Category>,
  dropdownSelection: string,
  searchInput: string,
  showNavigation: boolean,
  openedDropdown: string,
}

export default defineComponent({
  name: 'Header',

  components: {
    Dropdown,
    Search,
  },

  computed: {
    ...mapState([
      'voices',
      'favorites',
    ]),
  },

  methods: {
    ...mapMutations([
      'sortByAlphabet',
      'sortByAlphabetReverse',
      'updateCategoryTag',
    ]),

    setOpenedDropdown(dropdown: string) {
      // eslint-disable-next-line no-unused-expressions
      this.openedDropdown === dropdown
        ? this.openedDropdown = ''
        : this.openedDropdown = dropdown;
    },

    handleShowNavigation() {
      this.openedDropdown = '';
      this.showNavigation = !this.showNavigation;
    },

    scrollToTop() {
      window.scrollTo(0, 0);
    },

    handleDropdown(category: Category) {
      this.updateCategoryTag(category.tag);
      this.dropdownSelection = category.category;
    },
  },

  data(): Data {
    return {
      CATEGORIES,
      dropdownSelection: 'All songs',
      searchInput: '',
      showNavigation: true,
      openedDropdown: '',
    };
  },
});
